import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
import interfaceurl from '@/utils/interface.js'
export default {
	computed: mapState(['key']),
	data() {
		return {
			focus: true,
			keyword: '' //关键词
		}
	},
	onLoad() {
		this.keyword = this.key;
	},
	methods: {
		turn() {
			this.$store.commit('setKey', this.keyword);
			this.$turnPage('../../shop/index?key=' + this.keyword, 'switchTab');
		}
	}
}
